@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@300&display=swap);
body{margin:0;color:#697077;font-family:"Raleway", system-ui, sans-serif;background-color:#f2f4f8;-moz-osx-font-smoothing:grayscale;-webkit-font-smoothing:antialiased}main{display:flex;position:fixed;top:50%;left:50%;align-items:center;flex-direction:column;justify-content:center;opacity:1;transform:translate3d(-50%, -50%, 0);transition-duration:0.7s;transition-property:opacity, transform;transition-timing-function:cubic-bezier(0, 0, 0.3, 1)}.loading{opacity:0;transform:translate3d(-50%, 0, 0)}h1{margin-top:0;margin-bottom:1rem;font-size:5.75rem;font-weight:300;letter-spacing:-.04rem;line-height:6.375rem}

